// Custom Theming for Angular Material
@use '~@angular/material' as mat;
// For more information: https://material.angular.io/guide/theming
@import '~@angular/material/theming';
// Plus imports for other components in your app.

// Include the common styles for Angular Material. We include this here so that you only
// have to load a single css file for Angular Material in your app.
// Be sure that you only ever include this mixin once!
@include mat.core();
$bv-orange: (50: #2ca4dd,
  100: #00acee,
  200: #fecdbd,
  300: #fc9977,
  400: #fc8259,
  500: #fb6c3b,
  600: #fa551d,
  700: #f44205,
  800: #d63a04,
  900: #b83204,
  A100: #ffffff,
  A200: #fff7f4,
  A400: #fc8259,
  A700: #f44205,
  NewColor:#fff,
  contrast: (50: white,
    100: $black-87-opacity,
    200: $black-87-opacity,
    300: $black-87-opacity,
    400: $black-87-opacity,
    500: white,
    600: white,
    700: white,
    800: white,
    900: white,
    A100: $black-87-opacity,
    A200: $black-87-opacity,
    A400: $black-87-opacity,
    A700: white,
  ));
// Define the palettes for your theme using the Material Design palettes available in palette.scss
// (imported above). For each palette, you can optionally specify a default, lighter, and darker
// hue. Available color palettes: https://material.io/design/color/
$GSDashboard-primary: mat.define-palette($bv-orange, 50);
$GSDashboard-accent: mat.define-palette($bv-orange, 100);

// The warn palette is optional (defaults to red).
$GSDashboard-warn: mat.define-palette(mat.$red-palette);
$MyNewThemeColor : #2ca4dd;
// Create the theme object (a Sass map containing all of the palettes).
$GSDashboard-theme: mat.define-light-theme($GSDashboard-primary, $GSDashboard-accent, $GSDashboard-warn);
@include mat.all-component-themes($GSDashboard-theme);

html,
body {
  height: 100%;
}

body {
  margin: 0;
  //font-family: Roboto, "Helvetica Neue", sans-serif;
  font-family: "Montserrat",Helvetica,Arial,serif !important;
}

/* Importing Bootstrap SCSS file. */
@import '~bootstrap/scss/bootstrap';
@include mat.core();


// @font-face {
//     font-family: "Montserrat";
//     src: url("./assets/fonts/Montserrat-Regular.otf") format("opentype"),
//     }
// *{
//     font-family: 'Montserrat';

// }
.mat-dialog-container {
  padding: 0px !important;
  overflow: hidden !important;
}

.color-primary {
  color: mat.get-color-from-palette($GSDashboard-primary);
}

.bg-color-primary {
  background-color: $MyNewThemeColor !important;
}

.FitColumn {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px $MyNewThemeColor;
  border-radius: 5px;
}

::-webkit-scrollbar-track:hover {
  box-shadow: inset 0 0 15px $MyNewThemeColor;
  border-radius: 15px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: $MyNewThemeColor;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #00acee;
  border-radius: 15px;
}

.dx-datagrid .dx-row-alt>td,
.dx-datagrid .dx-row-alt>tr>td {
  background-color: #DCDCDC !important;
  color: black !important;
}

.dx-datagrid-headers {
  background-color:$MyNewThemeColor !important;
  color: white !important;
}

.dx-datagrid .dx-column-indicators .dx-header-filter.dx-header-filter,
.dx-datagrid .dx-column-indicators .dx-header-filter.dx-sort,
.dx-datagrid .dx-column-indicators .dx-sort.dx-header-filter,
.dx-datagrid .dx-column-indicators .dx-sort.dx-sort {
  color: white !important;
}

.mat-tab-label {
  background: $MyNewThemeColor !important;
  opacity: 100 !important;
  margin-left: 2px;
  margin-right: 2px;
  color: white !important;
  border-top-left-radius: 10px !important;
  border-top-right-radius: 10px !important;
}

.mat-tab-body-content {
  overflow: hidden !important;
}

.dx-header-row {
  background: $MyNewThemeColor !important;
  color: white !important;
}


::ng-deep .container-fluid.custom-padding{
  margin-top: 6.6rem;
}

::ng-deep .CustomNoficationPanelWidth{
  width: 350px;
}
.DefaultUserPanelPosition{
  position: absolute !important;
  top: 70px !important;
  right: 80px !important;
}

::ng-deep.Div_customCard .CustomFormPanel {
  border: 2px solid #e3e1e1;
  padding: 1%;
  background-color: #f9f9f9;
  height: 70vh;
  border-radius: 5px;
  box-shadow: 0 5px 10px #e2eefd;
  overflow: hidden;
  overflow-y: auto;
  border-bottom: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.CustomHeadingDiv {
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: $MyNewThemeColor !important;
}

.CustomHeadingDiv .Spanicon {
  font-size: 1.2rem;
}

.CustomBreadCrumbs {
  font-size: 0.7rem;
  text-align: right;
}

::ng-deep .btn{
  font-weight: 500!important;

}
::ng-deep .btn:active,
.btn:focus, .btn:focus-within{
  outline: none !important;
  box-shadow: none !important;
}

::ng-deep .btn-primary {
  border-color: $MyNewThemeColor!important;
  background-color: $MyNewThemeColor!important;
}


::ng-deep .btn-success {
  border-color: #16E27F!important;
  background-color: #16E27F!important;
}

::ng-deep .btn-primary:hover:not(.disabled):not(:disabled) {
  outline: none !important;
  box-shadow: none !important;
}

@media (max-width: 960px) {
  .DefaultUserPanelPosition {
    top: 75px !important;
    right: 15px !important;
  }
}

@media (max-width: 767px) {

  .CustomHeadingDiv,
  .CustomBreadCrumbs {
    text-align: center;
    padding-top: 0.5em;
  }
}
@media screen and (max-width:961px) {
  .CustomBreadCrumbs{
    display: none !important;
  }
}



.font-small-4 {
  font-size: 1rem!important;
}
.font-small-3 {
  font-size: .9rem!important;
}
.font-small-2 {
  font-size: .8rem!important;
}
.font-small-1 {
  font-size: .7rem!important;
}

::ng-deep .h1,
.h2, .h3,
.h4, .h5,
.h6, h1,
h2, h3,
h4, h5, h6 {
  font-weight: 500;
  line-height: 1.2;
  color: #5e5873 !important;
}

::ng-deep .h1,::ng-deep h1 {
  font-size: 2rem !important;
}

.text-muted{
  color: #6c757d !important;
}

.h2, h2 {
  font-size: 1.714em !important;
}
.h3,h3 {
  font-size: 1.5em !important;
}
.h4,h4 {
  font-size: 1.286em !important;
}
.h5,h5 {
  font-size: 1.07em !important;
}
.h6,h6 {
  font-size: 1em !important;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0.5rem;
  color: #5E5873;
}

.Table-Shadow {
  box-shadow: 0 4px 24px 0 rgba(34,41,47,0.1);
}


.btn-info{
  background-color: #F18F24 !important;
  color: white !important;
  border: 1px solid #F18F24 !important;
}


.mat-button,
.mat-raised-button,
.mat-icon-button,
.mat-stroked-button,
.mat-flat-button,
.mat-fab,
.mat-mini-fab{
  font-family: "Montserrat",Helvetica,Arial,serif !important;
  padding: 0.3rem;
}

.btn-danger,
.btn-danger:focus,
.btn-danger:focus-within {
  outline: none !important;
  box-shadow: none !important;
  border-color: #ea5455!important;
  background-color: #ea5455!important;
}
.btn-outline-dark,
.btn-outline-dark:focus,
.btn-outline-dark:focus-within{
  outline: none !important;
  box-shadow: none !important;
}

.mat-expansion-panel-header,
.mat-expansion-panel-header.mat-expanded,
.mat-expansion-panel-header.mat-expanded:focus,
.mat-expansion-panel-header.mat-expanded:hover{
  background-color: #fff !important;
}
.mat-expansion-panel-header.mat-expanded{
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.mat-expansion-panel-header-title,
.mat-expansion-indicator::after{
  color: $MyNewThemeColor !important;
}
